@import "../Router/utilities";

.slider {
  background-image: url("../Assets/home-bg.jpg");
  position: relative;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 70vh;

  .slick-dots li.slick-active button:before {
    color: $groco-main;
    margin-bottom: 5rem;
  }

  .slick-prev:before,
  .slick-next:before {
    color: $groco-main;
    width: 15px;
  }
  .slick-prev {
    left: 1%;
    top: 50%;
  }
  .slick-next {
    position: absolute;
    right: 1%;
    top: 50%;
  }
  @media (max-width: 820px) {
    height: 72vh;
  }
  .slides {
    .slider_wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 4rem;

      @media (max-width: 820px) {
        display: flex;
        margin-left: 4%;
        padding: 1rem;
        align-items: flex-start;
        flex-direction: column;
      }
      @media (max-width: 768px) {
        display: flex;
        padding: 1rem;
        align-items: flex-start;
        flex-direction: column;
      }
      @media (max-width: 412px) {
        display: flex;
        padding: 1rem;
        align-items: flex-start;
        flex-direction: column;
      }
      @media (max-width: 400px) {
        display: flex;
        padding: 1rem;
        align-items: flex-start;
        flex-direction: column;
      }

      .wrapper {
        animation: fadeLeft 0.4s linear 0.4s backwards;
        // flex: 1;

        @media (max-width: 820px) {
          font-size: 5rem;
        }
        @media (max-width: 768px) {
          font-size: 3rem;
          padding: 0% 0 0% 3%;
          display: block;
        }
        @media (max-width: 720px) {
        }
        @media (max-width: 400px) {
          display: inline-block;
          padding: 2 % 0 2% 8%;
        }
        @media (max-width: 412px) {
          display: inline-block;
        }

        span {
          color: $groco_gray;
          font-size: 1.6rem;
          @media (max-width: 820px) {
            font-size: 2rem;
          }
          @media (max-width: 768px) {
            font-size: 2rem;
            // margin-top: 1%;
          }
          @media (max-width: 412px) {
            font-size: 2rem;
            // margin-top: 7%;
          }
          @media (max-width: 400px) {
            font-size: 1.5rem;
            // margin-top: 2%;
          }
          @media (max-width: 320px) {
            font-size: 1.3rem;
            margin-top: 3%;
          }
        }

        h3 {
          font-size: 4.5rem;
          padding: 4% 0;
          @media (max-width: 820px) {
            font-size: 4rem;
            padding: 0;
          }
          @media (max-width: 768px) {
            font-size: 5rem;
            padding-bottom: 0.5rem;
          }
          @media (max-width: 412px) {
            font-size: 2.2rem;
            padding: 4% 0 4% 0;
          }
          @media (max-width: 400px) {
            font-size: 2rem;
            padding: 2% 0 2% 0;
          }
          @media (max-width: 320px) {
            font-size: 2.5rem;
            padding: 1% 0 3% 0;
          }
        }
        button {
          padding: 3% 7%;
          background-color: $groco-main;
          font-size: 1.1rem;
          border: none;
          color: $groco-white;
          @media (max-width: 820px) {
            padding: 4% 12%;
            font-size: 1.5rem;
            margin-bottom: 1rem;
          }
          @media (max-width: 768px) {
            padding: 3% 10%;
          }
          @media (max-width: 412px) {
            padding: 4% 10%;
            font-size: 1rem;
          }
          @media (max-width: 400px) {
            padding: 4% 10%;
            font-size: 1rem;
          }
          @media (max-width: 320px) {
            padding: 4% 10%;
            font-size: 1rem;
          }
        }
      }
      .slider__image {
        @media (max-width: 820px) {
          width: 65%;
          margin-left: 5%;
          margin-top: 2rem;
        }
        @media (max-width: 768px) {
          width: 65%;
          margin-left: 5%;
          margin-top: 1rem;
        }

        @media (max-width: 504px) {
          width: 100%;
          margin: 0;
        }
        @media (max-width: 412px) {
          width: 90%;
        }
        @media (max-width: 400px) {
          width: 100%;
        }

        img {
          animation: fadeUp 0.4s linear;
          @media (max-width: 768px) {
            width: 100%;
          }
          @media (max-width: 504px) {
            width: 100%;
          }
          @media (max-width: 412px) {
            width: 100%;
          }
          @media (max-width: 400px) {
            width: 100%;
          }
        }
      }
      .slider__image1 {
        @media (max-width: 820px) {
          width: 70%;
          margin-left: 5%;
          // margin-top: 2rem;
        }
        @media (max-width: 768px) {
          width: 70%;
          margin-left: 5%;
          // margin-top: 2rem;
        }

        @media (max-width: 504px) {
          width: 100%;
          margin: 0;
        }
        @media (max-width: 412px) {
          width: 90%;
        }
        @media (max-width: 400px) {
          width: 100%;
        }

        img {
          animation: fadeUp 0.4s linear;
          @media (max-width: 820px) {
            width: 100%;
          }
          @media (max-width: 768px) {
            width: 100%;
          }
          @media (max-width: 504px) {
            width: 100%;
          }
          @media (max-width: 412px) {
            width: 100%;
          }
          @media (max-width: 400px) {
            width: 100%;
          }
        }
      }
      .slider__image2 {
        @media (max-width: 820px) {
          width: 75%;
          margin-left: 5%;
          // margin-top: 2rem;
        }
        @media (max-width: 768px) {
          width: 75%;
          margin-left: 5%;
          // margin-top: 2rem;
        }

        @media (max-width: 504px) {
          width: 100%;
          margin: 0;
        }
        @media (max-width: 412px) {
          width: 90%;
        }
        @media (max-width: 400px) {
          width: 100%;
        }

        img {
          animation: fadeUp 0.4s linear;
          @media (max-width: 820px) {
            width: 100%;
          }
          @media (max-width: 768px) {
            width: 100%;
          }
          @media (max-width: 504px) {
            width: 100%;
          }
          @media (max-width: 412px) {
            width: 100%;
          }
          @media (max-width: 400px) {
            width: 100%;
          }
        }
      }
    }
  }
}

// HOME2 STYLING
.banner {
  width: 80%;
  @media (max-width: 768px) {
    width: 90%;
  }
  @media (max-width: 820px) {
    width: 90%;
  }
  @media (max-width: 768px) {
    width: 90%;
  }
  margin: 2% auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  @media (max-width: 820px) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  @media (max-width: 768px) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  @media (max-width: 400px) {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  @media (max-width: 412px) {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  .main {
    position: relative;
    padding: 2%;
    overflow: hidden;
    @media (max-width: 400px) {
      padding: 5% 0 0% 0;
      width: 100%;
    }

    img {
      width: 100%;
      object-fit: cover;
      &:hover {
        transform: scale(1.2);
      }
    }
    .txt {
      position: absolute;
      top: 30%;
      left: 6%;
      span {
        font-size: 1.1rem;
        color: $groco_gray;
        @media (max-width: 768px) {
          font-size: 1.2rem;
        }
        @media (max-width: 400px) {
          font-size: 1rem;
        }
      }
      h3 {
        font-size: 1.45rem;
        padding: 5% 0 10% 0;
        @media (max-width: 768px) {
          font-size: 1.2rem;
        }
        @media (max-width: 400px) {
          font-size: 1.2rem;
        }
        @media (max-width: 320px) {
          font-size: 1rem;
        }
      }
      button {
        padding: 9% 18%;
        border: none;
        font-size: 1.1rem;
        color: $groco-white;
        background-color: $groco-main;
        @media (max-width: 768px) {
          padding: 6% 15%;
          font-size: 1rem;
        }
        @media (max-width: 400px) {
          padding: 6% 15%;
          font-size: 1rem;
        }
        @media (max-width: 320px) {
          padding: 6% 15%;
          font-size: 0.9rem;
        }
      }
    }
  }
}
@keyframes fadeLeft {
  0% {
    transform: translateX(-5rem);
    opacity: 0;
  }
}
@keyframes fadeUp {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
}
